<template>
  <div class="common-page">
    <TopLoading :isLoading="loading"> </TopLoading>
    <div :style="{width:$store.getters.contentWidth+'px'}" class="container">
      <section class="filter-container">
        <div class="filterleft" :class="{'expand':expandFilter}">
          <SelectPiece name="" :options="types" @change="typeChange"></SelectPiece>
          <SelectPiece name="" :options="regions" @change="regionChange"></SelectPiece>
          <SelectPiece name="" :options="years" @change="yearChange"></SelectPiece>
          <SelectPiece name="" :options="feeTypes" @change="feeTypeChange"></SelectPiece>
        </div>
        <div class="filterright">
          <span @click="expandFilter=!expandFilter" :class="{'active':expandFilter}" class="expand-filter">
            <span v-show="expandFilter"></span>
            <span v-show="!expandFilter"></span>
            <i class="material-icons">expand_more</i>
          </span>
        </div>
      </section>

      <div class="devider"></div>
      <section class="top-bar">
        <div class="left">
          <span class="piece active"></span>
          <span class="piece"></span>
          <span class="piece"></span>
        </div>
        <div class="right">
          
          <span class="number">5000</span>
           1/56
        </div>
      </section>
      <section class="videos-container">
        <VideoCard v-for="(video,index) in videos" :key="index" :video-info="video"></VideoCard>
      </section>
    </div>
  </div>
</template>

<script>
import VideoCard from "@/components/VideoCard";
import SelectPiece from "@/components/SelectPiece";
import TopLoading from "@/components/TopLoading";

import getVideos from "@/api";
export default {
  name: "CommonPage",
  data() {
    return {
      msg: "common page",
      expandFilter: false,
      loading: false,
      types: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      regions: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      years: [
        "",
        "2017",
        "2016",
        "2015",
        "2014",
        "2013-2011",
        "2010-2006",
        "2005-2000",
        "90",
        "80",
        ""
      ],
      feeTypes: ["", "", ""],
      videos: []
    };
  },
  beforeRouteUpdate(to, from, next) {
    this.updateVideos();
    next();
  },
  methods: {
    updateVideos: function() {
      let self = this;
      self.loading = true;
      getVideos(100).then(function(res) {
        self.videos = res;
        self.loading = false;
      });
    },
    typeChange: function(value) {
      this.updateVideos();
    },
    feeTypeChange: function(value) {
      this.updateVideos();
    },
    regionChange: function(value) {
      this.updateVideos();
    },
    yearChange: function(value) {
      this.updateVideos();
    }
  },
  components: {
    TopLoading,
    VideoCard,
    SelectPiece
  },
  mounted() {
    this.updateVideos();
    console.log(this.$router);
  }
};
</script>
<style  src="./Index.scss" lang="scss"></style>



